import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import FontHelp from '@/components/FontHelp'
import Index from '@/components/Index'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'index',
      component: Index
    }, {
      path: '/test',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/fontHelp',
      name: 'FontHelp',
      component: FontHelp
    }
  ]
})


